
import XCircle from 'vux/src/components/x-circle/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
import * as util from '../api/common'

export default {
components: {
XCircle, Cell, Group
},
data () {
return {
percent: 100,
account_num: 0.00, // 总信用
use_account: 0.00, // 可使用额度
debt_account_num: 0.00, // 欠款--已使用
refund_account_num: 0.00, // 应归还
refund_time: '06/23', // 还款日
userType: localStorage.getItem('userType')
}
},
mounted () {
this.getData()
},
methods: {
getData () {
let params = new URLSearchParams()
var self = this

var instance = axios.create({ baseURL: util.baseURL })

instance.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
instance.post('buyer/center', params).then(res => {
console.log(res)
if (res.data) {
self.account_num = res.data.data.account_num
self.use_account = res.data.data.use_account
self.debt_account_num = res.data.data.debt_account_num
self.refund_account_num = res.data.data.refund_account_num
self.refund_time = res.data.data.refund_time
self.percent = self.use_account / self.account_num * 100
} else {

}
})
}
}
}



